@charset "UTF-8";
/* -----样式变量---- */
/* -----版本1.0---- */
/* -----日期2016.3.31---- */
.u_panel {
  background-color: #fff;
  border: 1px solid #eee;
  border-radius: 3px;
  -webkit-box-shadow: none;
  box-shadow: none;
  overflow: hidden;
}

.u_panel.u_panel_full {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9;
  min-height: 100%;
}

.u_panel .u_panel_head {
  height: 40px;
  border-bottom: 1px solid #eeeeee;
  background-color: #f9fafb;
  padding: 0 10px;
  line-height: 38px;
  color: #888;
}

.u_panel .u_panel_head .u_panel_icon {
  font-size: 18px;
  float: left;
  margin-top: 1px;
  margin-left: 0px;
}

.u_panel .u_panel_head .u_panel_title {
  font-size: 14px;
  float: left;
  font-weight: normal;
}

.u_panel .u_panel_head > .up-pull-right > a {
  color: #c0c2cd;
  font-size: 12px;
}

.u_panel .u_panel_head > .up-pull-right > a:hover, .u_panel .u_panel_head > .up-pull-right > a:focus {
  color: #c0c2cd;
  text-decoration: none;
}

.u_panel .u_tab_nav.u_panel_head {
  padding: 0;
}

.u_panel .u_tab_nav.u_panel_head .u_tab_nav_body {
  border-bottom: 0;
}

.u_panel .u_tab_nav.u_panel_head li:first-of-type {
  border-left: 0;
}

.u_panel .u_tab_nav.u_panel_head li:not(.active) {
  border-top: 0;
  padding-top: 2px;
}

.u_panel .u_tab_nav.u_panel_head li {
  line-height: 40px;
  height: 40px;
}

.u_panel .u_tab_nav_dark.u_panel_head .u_tab_nav_body {
  border-bottom: 2px solid #33ced0;
}

.u_panel .u_tab_nav_dark.u_panel_head li {
  line-height: 37px;
  height: 37px;
}

.u_panel .u_panel_body {
  padding: 10px;
  cursor: default;
  width: 100%;
}

.u_panel_box {
  padding: 5px;
}

.u_panel_box[data-origstyle] {
  padding: 0;
}

.u_panel_placeHolder {
  margin: 5px 4px 5px 5px;
  border: 1px dotted #ccc;
  float: left;
  background: transparent;
}

.u_panel_accordion .u_panel_body {
  display: none;
}
